<template>
  <b-sidebar
    id="add-new-user-sidebar"
    :visible="isAddNewUserSidebarActive"
    bg-variant="white"
    sidebar-class="sidebar-lg"
    shadow
    backdrop
    no-header
    right
    @hidden="resetForm"
    @change="(val) => $emit('update:is-add-new-user-sidebar-active', val)"
  >
    <template #default="{ hide }">
      <!-- Header -->
      <div class="d-flex justify-content-between align-items-center content-sidebar-header px-2 py-1">
        <h5 class="mb-0">
          Agregar Nuevo Usuario
        </h5>

        <feather-icon
          class="ml-1 cursor-pointer"
          icon="XIcon"
          size="16"
          @click="hide"
        />

      </div>

      <!-- BODY -->
      <validation-observer

        ref="refFormObserver"
      >
        <!-- Form -->
        <b-form
          ref="formUser"
          class="p-2"
          @submit.prevent="addUser"
          @reset.prevent="resetForm"
        >

          <!-- Centro de Costo -->
          <validation-provider
            #default="validationContext"
            name="Centro de Costo"
            rules="required"
          >
            <b-form-group
              label="Centro de Costos"
              label-for="centro_costos"
              :state="getValidationState(validationContext)"
            >
              <v-select
                v-model="user.cost_center_id"
                multiple
                label="description"
                :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
                :options="costcenters"
                :reduce="costcenters => costcenters.id"
                :clearable="false"
                input-id="centro_costos"
                placeholder="Seleccione el centro de costo"
              />
              <b-form-invalid-feedback :state="getValidationState(validationContext)">
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>

          <!-- Dependencia -->
          <validation-provider
            #default="validationContext"
            name="Dependencia"
            rules="required"
          >
            <b-form-group
              label="Dependencia"
              label-for="dependencia"
              :state="getValidationState(validationContext)"
            >
              <v-select
                v-model="user.id_dependence"
                label="description"
                :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
                :options="dependences"
                :reduce="dependences => dependences.id"
                :clearable="false"
                input-id="dependencia"
                placeholder="Seleccione la dependencia"
              />
              <b-form-invalid-feedback :state="getValidationState(validationContext)">
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>

          <!-- Perfiles -->
          <validation-provider
            #default="validationContext"
            name="Perfil"
            rules="required"
          >
            <b-form-group
              label="Perfil"
              label-for="perfil"
              :state="getValidationState(validationContext)"
            >
              <v-select
                v-model="user.id_profile"
                label="name"
                :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
                :options="profiles"
                :reduce="profiles => profiles.id"
                :clearable="false"
                input-id="perfil"
                placeholder="Seleccione el perfil"
              />
              <b-form-invalid-feedback :state="getValidationState(validationContext)">
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>

          <!-- Nombre -->
          <validation-provider
            #default="validationContext"
            name="Nombre"
            rules="required|alpha_accents"
          >
            <b-form-group
              label="Nombre del Usuario"
              label-for="name"
            >
              <b-form-input
                id="name"
                v-model="user.name"
                autofocus
                :state="getValidationState(validationContext)"
                trim
                placeholder="Nombre Completo"
              />

              <b-form-invalid-feedback>
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>

          <!-- Email -->
          <validation-provider
            #default="validationContext"
            name="Email"
            rules="required|email"
          >
            <b-form-group
              label="Email"
              label-for="email"
            >
              <b-form-input
                id="email"
                v-model="user.email"
                :state="getValidationState(validationContext)"
                trim
                placeholder="Email"
              />

              <b-form-invalid-feedback>
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>

          <!-- Username -->
          <validation-provider
            #default="validationContext"
            name="Username"
            rules="required|alpha_point"
          >
            <b-form-group
              label="Username"
              label-for="username"
            >
              <b-form-input
                id="username"
                v-model="user.username"
                :state="getValidationState(validationContext)"
                trim
                placeholder="Username"
              />

              <b-form-invalid-feedback>
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>

          <!-- Password -->
          <validation-provider
            #default="validationContext"
            name="Contraseña"
            rules="required|password"
          >
            <b-form-group
              label="Contraseña"
              label-for="password"
            >
              <b-form-input
                id="password"
                v-model="user.password"
                type="password"
                :state="getValidationState(validationContext)"
                trim
                placeholder="Contraseña"
              />

              <b-form-invalid-feedback>
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>

          <!-- User Role -->
          <!-- <validation-provider
            #default="validationContext"
            name="User Role"
            rules="required"
          >
            <b-form-group
              label="User Role"
              label-for="user-role"
              :state="getValidationState(validationContext)"
            >
              <v-select
                v-model="userData.role"
                :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
                :options="roleOptions"
                :reduce="val => val.value"
                :clearable="false"
                input-id="user-role"
              />
              <b-form-invalid-feedback :state="getValidationState(validationContext)">
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider> -->

          <!-- Form Actions -->
          <div class="d-flex mt-2">
            <b-button
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              variant="primary"
              class="mr-2"
              type="submit"
            >
              Agregar
            </b-button>
            <b-button
              v-ripple.400="'rgba(186, 191, 199, 0.15)'"
              type="button"
              variant="outline-secondary"
              @click="clearForm"
            >
              Cancelar
            </b-button>
          </div>

        </b-form>
      </validation-observer>
    </template>
  </b-sidebar>
</template>

<script>
/* eslint-disable */
import {
  BSidebar, BForm, BFormGroup, BFormInput, BFormInvalidFeedback, BButton,
} from 'bootstrap-vue'
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import { ref } from '@vue/composition-api'
import { required, alphaNum } from '@validations'
import formValidation from '@core/comp-functions/forms/form-validation'
import Ripple from 'vue-ripple-directive'
import vSelect from 'vue-select'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
// import countries from '@/@fake-db/data/other/countries'
// import store from '@/store'

// Notificaciones

// Servicios
import * as serviceCostCenter from '@/services/cost_center'
import * as serviceDependence from '@/services/dependence'
import * as serviceProfile from '@/services/profile'
import * as serviceUser from '@/services/user'

export default {
  components: {
    BSidebar,
    BForm,
    BFormGroup,
    BFormInput,
    BFormInvalidFeedback,
    BButton,
    vSelect,
    ToastificationContent,

    // Form Validation
    ValidationProvider,
    ValidationObserver,
  },
  directives: {
    Ripple,
  },
  model: {
    prop: 'isAddNewUserSidebarActive',
    event: 'update:is-add-new-user-sidebar-active',
  },
  props: {
    isAddNewUserSidebarActive: {
      type: Boolean,
      required: true,
    },
    roleOptions: {
      type: Array,
      required: true,
    },
    planOptions: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      required,
      alphaNum,
      costcenters: [],
      dependences: [],
      profiles: [],
      user: {
        cost_center_id: [],
        id_dependence: undefined,
        id_profile: undefined,
        name: '',
        email: '',
        username: '',
        password: '',
      },
    }
  },

  created() {
    this.getCostCentersActive()
    this.getDependences()
    this.getProfiles()
  },

  methods: {

    async getCostCentersActive() {
      try {

        let active = true
        const response = await serviceCostCenter.getCostCentersActive(active)

        if (response.status === 200) {
          this.costcenters = response.data.data
        }
      } catch (error) {
        this.showToast('Alerta', 'AlertTriangleIcon', 'No se puedieron obtener los datos', 'warning')
      }
    },

    async addUser() {
      try {
        const response = await serviceUser.addUser(this.user)

        if (response.status === 200) {
          this.showToast('Agregado', 'CheckIcon', 'Usuario Agregado', 'success')
          this.$emit('refetch-data')
          this.clearForm()
        }
      } catch (error) {
        this.showToast('Alerta', 'AlertTriangleIcon', 'No se pudo agregar el usuario', 'warning')
      }
    },

    async getDependences() {
      try {
        const response = await serviceDependence.getDependencies()

        if (response.status === 200) {
          this.dependences = response.data.data
        }
      } catch (error) {
        this.showToast('Alerta', 'AlertTriangleIcon', 'No se puedieron obtener los datos', 'warning')
      }
    },

    async getProfiles() {
      try {
        const response = await serviceProfile.getProfilesActive()

        if (response.status === 200) {
          this.profiles = response.data.data
        }
      } catch (error) {
        this.showToast('Alerta', 'AlertTriangleIcon', 'No se puedieron obtener los datos', 'warning')
      }
    },

    clearForm() {
      this.$refs.formUser.reset();
      this.$emit('update:is-add-new-user-sidebar-active', false)
      this.user = {
        cost_center_id: [],
        id_dependence: undefined,
        id_profile: undefined,
        name: '',
        email: '',
        username: '',
        password: '',
      }
    },

    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },

  },

  setup(props, { emit }) {
    const blankUserData = {
      cost_center_id: [],
      id_dependence: null,
      id_profile: null,
      name: '',
      email: '',
      username: '',
      password: '',
    }

    const userData = ref(JSON.parse(JSON.stringify(blankUserData)))
    const resetuserData = () => {
      userData.value = JSON.parse(JSON.stringify(blankUserData))
    }

    // const onSubmit = () => {
    //   store.dispatch('app-user/addUser', userData.value)
    //     .then(() => {
    //       emit('refetch-data')
    //       emit('update:is-add-new-user-sidebar-active', false)
    //     })
    // }

    // emit('update:is-add-new-user-sidebar-active', false)

    const {
      refFormObserver,
      getValidationState,
      resetForm,
    } = formValidation(resetuserData)

    return {
      userData,
      // onSubmit,

      refFormObserver,
      getValidationState,
      resetForm,
    }
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-select.scss';

#add-new-user-sidebar {
  .vs__dropdown-menu {
    max-height: 200px !important;
  }
}
</style>
